



















































import { Component, Vue } from 'vue-property-decorator';
import { Divider } from 'vant';

import 'vant/lib/divider/style';

Vue.use(Divider);

@Component
export default class Original extends Vue {

    public signedList = Array();


    public created(): void {

        for (let i = 0; i < this.$store.state.data.origins.length; i++) {
            if (this.$store.state.data.origins[i].status == '已签名') {
                this.signedList.push(this.$store.state.data.origins[i]);
            }
        }
        this.$store.state.title = this.$t('original.process');
    }
}
